<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

const toLower = (text) => {
  return text.toString().toLowerCase();
};
// 搜尋條件
const searchByName = (items, term) => {
  if (term) {
    return items.filter((item) => toLower(item.info).includes(toLower(term)));
  }
  return items;
};

export default {
  name: "pendingFilesComponent",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      // 共用
      tabName: "",
      tabPaneName: "工程申請單",
      searchInput: "",
      form: {
        rr_Id: "",
        sr_Result: "",
        Sr_Comment: "",
        task_Id: "",
      },
      rules: {
        sr_Result: [{ required: true, message: "必填", trigger: "change" }],
      },

      // 工程申請單
      projectApplyTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "task_Id",
          label: "簽核任務編號",
          width: "200",
        },
        {
          id: "info",
          label: "工程編號/工程名稱",
          width: "200",
        },
        {
          id: "createDate",
          label: "建立日期(開始日期~結束日期)",
          width: "200",
        },
        {
          id: "area",
          label: "工作區域",
          width: "200",
        },
        {
          id: "sInfo",
          label: "主承包商編號/主承包商名稱/下承包商數",
          width: "200",
        },
        {
          id: "status",
          label: "工程狀態",
          width: "150",
        },
      ],
      prjAssignData: [],
      prjData: [],
      prjList: [],
      prjCurrentPage: 1,
      projectSelectedLists: [],

      // 每日施工單
      dailyWorkTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "applyPk_taskId",
          label: "每日施工單號/簽核任務編號",
          width: "200",
        },
        {
          id: "info",
          label: "工程編號/工程名稱",
          width: "200",
        },
        {
          id: "createDate",
          label: "建立日期(開始日期~結束日期)",
          width: "200",
        },
        {
          id: "area",
          label: "施工日期/工作區域",
          width: "200",
        },
        {
          id: "workType",
          label: "施工種類",
          width: "200",
        },
        {
          id: "sInfo",
          label: "主承包商編號/主承包商名稱/下承包商數",
          width: "200",
        },
        {
          id: "status",
          label: "工程狀態",
          width: "150",
        },
      ],
      dailyAssignData: [],
      dailyData: [],
      dailyList: [],
      dailyCurrentPage: 1,
      dailyWorkSelectedLists: [],

      // 緊急搶修單
      emergencyRepairTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "task_Id",
          label: "簽核任務編號",
          width: "200",
        },
        {
          id: "info",
          label: "工程編號/工程名稱",
          width: "200",
        },
        {
          id: "createDate",
          label: "建立日期(開始日期~結束日期)",
          width: "200",
        },
        {
          id: "area",
          label: "工作區域",
          width: "200",
        },
        {
          id: "status",
          label: "工程狀態",
          width: "150",
        },
      ],
      emergencyAssignData: [],
      emergencyData: [],
      emergencyList: [],
      emergencyCurrentPage: 1,
      emergencyRepairSelectedLists: [],
    };
  },
  computed: {},
  methods: {
    // 公用
    searchHandler(val) {
      if (this.searchInput) {
        if (this.tabPaneName === "工程申請單") {
          this.prjData = searchByName(this.prjAssignData, val);
          this.prjList = this.prjData.slice(0, 10);
        } else if (this.tabPaneName === "每日施工單") {
          this.dailyData = searchByName(this.dailyAssignData, val);
          this.dailyList = this.dailyData.slice(0, 10);
        } else {
          this.emergencyData = searchByName(this.emergencyAssignData, val);
          this.emergencyList = this.emergencyData.slice(0, 10);
        }
      } else {
        if (this.tabPaneName === "工程申請單") {
          this.prjData = this.prjAssignData;
          this.prjList = this.prjData.slice(0, 10);
          this.prjCurrentPage = 1;
        } else if (this.tabPaneName === "每日施工單") {
          this.dailyData = this.dailyAssignData;
          this.dailyList = this.dailyData.slice(0, 10);
          this.dailyCurrentPage = 1;
        } else {
          this.emergencyData = this.emergencyAssignData;
          this.emergencyList = this.emergencyData.slice(0, 10);
          this.emergencyCurrentPage = 1;
        }
      }
    },
    // 更換tab input 清空
    handleClick(tab) {
      const el = tab.$el.id;
      this.tabName = el.split("-")[1];
      this.searchInput = "";
    },
    // 條件判斷table的資料
    selectedHandleRemove(item) {
      const type = item.type;
      if (type === "prj") {
        this.projectSelectedLists = this.projectSelectedLists.filter(
          (e) => e.project_No !== item.project_No
        );
        this.$refs.prj.$refs["multipleTable"].toggleRowSelection(item);
      } else if (type === "daily") {
        this.dailyWorkSelectedLists = this.dailyWorkSelectedLists.filter(
          (e) => e.project_No !== item.project_No
        );
        this.$refs.daily.$refs["multipleTable"].toggleRowSelection(item);
      } else {
        this.emergencyRepairSelectedLists =
          this.emergencyRepairSelectedLists.filter(
            (e) => e.project_No !== item.project_No
          );
        this.$refs.emergency.$refs["multipleTable"].toggleRowSelection(item);
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const data = [
            ...this.projectSelectedLists,
            ...this.dailyWorkSelectedLists,
            ...this.emergencyRepairSelectedLists,
          ];
          const newData = this.dataSetRRId(data);
          this.$store
            .dispatch("VertifyPendingFile", newData)
            .then(async (res) => {
              if (res.data.isOK) {
                this.$message({
                  type: "success",
                  duration: 5000,
                  showClose: true,
                  message: "簽核成功",
                });
                this.projectSelectedLists = [];
                this.dailyWorkSelectedLists = [];
                this.emergencyRepairSelectedLists = [];
                this.prjCurrentPage = 1;
                this.dailyCurrentPage = 1;
                this.emergencyCurrentPage = 1;
                this.$refs.prj.$refs.multipleTable.clearSelection();
                this.$refs.daily.$refs.multipleTable.clearSelection();
                this.$refs.emergency.$refs.multipleTable.clearSelection();
                this.tabPaneName = "工程申請單";
                this.resetForm(formName);
                await this.getPrjPendingList();
                await this.getDailyPendingList();
                await this.getUrgentPendingList();
              }
            })
            .catch((err) => console.log(err));
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    dataSetRRId(data) {
      data.forEach((e) => {
        if (e.type === "prj" || e.type === "emergency") {
          e.rr_Id = e.project_No;
        } else if (e.type === "daily") {
          e.rr_Id = e.apply_Pk;
        }
        e.sr_Result = this.form.sr_Result;
        e.Sr_Comment = this.form.Sr_Comment;
      });
      return data;
    },

    // 工程申請單
    async getPrjPendingList() {
      const res = await this.$store.dispatch("QryPrjPendingList");
      const data = res.data.data.projList;
      this.prjAssignData = JSON.parse(JSON.stringify(data));
      this.prjAssignData.forEach((e, index) => {
        e.index = index + 1;
        e.info = `${e.project_No}/${e.project_Name}`;
        e.createDate = `${e.create_Date}(${e.begin_Date}~${e.end_Date})`;
        e.area = `${e.floor}/${e.ld2}(${e.ld1})/${e.ld4}(${e.ld3})`;
        e.sInfo = `${e.s_Id}/${e.s_Name}/${e.sub_Vendor_Num}`;
        e.status = this.convertStatus(e.project_Status);
        e.type = "prj";
      });
      this.prjData = this.prjAssignData;
      this.prjList = this.prjData.slice(0, 10);
    },
    prjChangePage(page) {
      this.prjCurrentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.prjList = this.prjData.slice(cur, last);
    },
    async viewSignOffDetails(prjNo) {
      let data = [];
      try {
        const getProjDetailRes = await this.$store.dispatch(
          "getProjDetail",
          prjNo
        );
        const getSignOffHistoryRes = await this.$store.dispatch(
          "getSignOffHistory",
          prjNo
        );
        data = [getProjDetailRes.data.data, getSignOffHistoryRes.data.data];
      } catch (err) {
        console.log(err);
      }

      // query 參數夾帶 JSON 字串，將資料傳給新頁面
      const { href } = this.$router.resolve({
        path: "/viewSignOffDetails",
        query: {
          data: JSON.stringify(data),
        },
      });
      window.open(href, "_blank");
    },
    projectSelectedHandler(item) {
      this.projectSelectedLists = item;
    },

    // 每日工作單
    async getDailyPendingList() {
      const res = await this.$store.dispatch("QryDailyPendingList");
      const data = res.data.data.dailyList;
      this.dailyAssignData = JSON.parse(JSON.stringify(data));
      this.dailyAssignData.forEach((e, index) => {
        e.index = index + 1;
        e.applyPk_taskId = `${e.apply_Pk}/${e.task_Id}`;
        e.info = `${e.project_No}/${e.project_Name}`;
        e.createDate = `${e.create_Date}(${e.begin_Date}~${e.end_Date})`;
        e.area = `${e.work_Date}/${e.floor}/${e.ld2}(${e.ld1})/${e.ld4}(${e.ld3})`;
        e.workType = `${e.wtn ? e.wtn : "一般作業"}`;
        e.sInfo = `${e.s_Id}/${e.s_Name}/${e.sub_Vendor_Num}`;
        e.status = this.convertStatus(e.project_Status);
        e.type = "daily";
      });
      this.dailyData = this.dailyAssignData;
      this.dailyList = this.dailyData.slice(0, 10);
    },
    dailyChangePage(page) {
      this.dailyCurrentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.dailyList = this.dailyData.slice(cur, last);
    },
    dailyWorkSelectedHandler(item) {
      this.dailyWorkSelectedLists = item;
    },
    viewSignOffDailyWork(apply_Pk) {
      window.open(`/#/pendingFileDailyDetail/${encodeURIComponent(apply_Pk)}`);
    },

    // 緊急搶修單
    async getUrgentPendingList() {
      const res = await this.$store.dispatch("QryUrgentPendingList");
      const data = res.data.data.urgentList;
      this.emergencyAssignData = JSON.parse(JSON.stringify(data));
      this.emergencyAssignData.forEach((e, index) => {
        e.index = index + 1;
        e.info = `${e.project_No}/${e.project_Name}`;
        e.createDate = `${e.create_Date}(${e.begin_Date}~${e.end_Date})`;
        e.area = `${e.ld2}(${e.ld1})/${e.ld4}(${e.ld3})/${e.floor}`;
        e.status = this.convertStatus(e.project_Status);
        e.type = "emergency";
      });
      this.emergencyData = this.emergencyAssignData;
      this.emergencyList = this.emergencyData.slice(0, 10);
    },
    emergencyChangePage(page) {
      this.emergencyCurrentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.emergencyList = this.emergencyData.slice(cur, last);
    },
    emergencyRepairSelectedHandler(item) {
      this.emergencyRepairSelectedLists = item;
    },
    async emergencyRepairViewSignOff(prjNo) {
      const res = await this.$store.dispatch("getViewSignOffDetail", prjNo);
      const data = res.data.data;
      const { href } = this.$router.resolve({
        path: "/emergencyRepairOrderViewSighOffDetails",
        query: {
          data: JSON.stringify(data),
        },
      });
      window.open(href, "_blank");
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    // 解決同步非同步 等待這三個跑完
    await this.getPrjPendingList();
    await this.getDailyPendingList();
    await this.getUrgentPendingList();
  },
};
</script>

<template src="./pendingFiles.html"></template>
